.product{
    display:flex;
    flex-direction: column;
    background-color: white;
    z-index: 1;
    align-items:center;
    justify-content:flex-end;
    max-height:400px;
    min-width:100px;
    margin: 10px;
    padding:20px;
    width:100%;
}

.product_image{
    max-height:200px;
    width:100%;
    object-fit:contain;
    margin-bottom:15px;
}

.product_rating{
    display:flex;
}

.product_title{
    height:35px;
    overflow: hidden;
    text-overflow: ellipsis;
}

.product_info{
    height:100px;
    margin-bottom:15px;
}

.product_price{
    margin-top: 5px;
}

.product > button{
    background-color: #f0c14b;
    border:1px solid;
    color:black;
    border-color: #a88734 #9c7e31 #846a29;
}