.checkoutProduct{
    display:flex;
    margin:20px 0;
}

.checkoutProduct_rating{
    display:flex;
}

.checkoutProduct_info{
    padding-left:20px;
}

.checkoutProduct_info>button{
    background-color: #f0c14b;
    border:1px solid;
    margin-top:10px;
    border-color:#a88734 #9c7e31 #846a29;
    color:black;
}

.checkoutProduct_image{
    object-fit: contain;
    width:180px;
    height:180px;
}

.checkoutProduct_title{
    font-size:17px;
    font-weight: bold;
}