.header{
    height:60px;
    display:flex;
    align-items: center;
    background-color: #131921;
    position:sticky;
    top:0%;
    z-index:100;
}

.header_logo{
    width:100px;
    object-fit: contain;
    margin:0 20px;
    margin-top: 18px;
}

.header_search{
    display:flex;
    flex:1;
    align-items: center;
    border-radius: 24px;
}

.header_searchInput{
    height:12px;
    padding:10px;
    border:none;
    width:100%;
}

.header_searchIcon{
    background-color: #cd9842;
    padding:5px;
    height:22px;
}

.header_nav{
    display:flex;
    justify-content: space-evenly;
}

.header_options{
    display:flex;
    flex-direction: column;
    margin:0 10px;
    color:white;
}

.header_optionLineOne{
    font-size:10px;
}

.header_optionLineTwo{
    font-size:13px;
    font-weight: bold;
}

.header_optionBasket{
    color:white;
    display:flex;
    align-items: center;
}

.header_basketCount{
    margin:0 10px;
}